@tailwind base;
@tailwind components;
@tailwind utilities;

@keyframes shimmer {
    0% {
      background-position: -1000px 0;
    }
    100% {
      background-position: 1000px 0;
    }
  }
  
  .shimmer {
    background: linear-gradient(
      90deg,
      rgba(255, 255, 255, 0.1) 25%,
      rgba(255, 255, 255, 0.2) 50%,
      rgba(255, 255, 255, 0.1) 75%
    );
    background-size: 1000px 100%;
    animation: shimmer 2s infinite linear;
    background-color: #333; /* Change this to match your background color */
  }
  
  .clip{
    clip-path: circle(40px at left 32px top 40px);
  }
  .noclip{
    clip-path: none;
  }